/* Animaciones suaves */
* {
  margin: 0;
  transition: background-color 0.3s ease, color 0.3s ease,
    border-color 0.3s ease;
}

:root {
  /* Paleta de colores principal */
  --primary-blue: #a8d8ea;
  --soft-violet: #c7ceea;
  --baby-blue: #e3f2fd;
  --lavender: #f3e5f5;
  --soft-purple: #ddd6fe;
  --light-blue: #e1f5fe;
  --accent-blue: #81c784;
  --text-primary: #2d3748;
  --text-secondary: #4a5568;
  --card-light: #ffffff;
  --card-shadow-light: rgba(168, 216, 234, 0.25);

  /* Modo oscuro */
  --dark-primary: #2d3748;
  --dark-secondary: #4a5568;
  --dark-accent: #667eea;
  --dark-violet: #9f7aea;
}

/* Estilos del cuerpo mejorados */
body.bg-light {
  background: linear-gradient(
    135deg,
    var(--baby-blue) 0%,
    var(--lavender) 30%,
    #ffffff 70%,
    var(--light-blue) 100%
  );
  color: var(--text-primary);
  min-height: 100vh;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
body.bg-dark {
  background: linear-gradient(135deg, #1a202c 0%, #2d3748 50%, #4a5568 100%);
  color: #e2e8f0;
  min-height: 100vh;
}
/* Secciones generales */
.section-title {
  font-size: 2.8rem;
  font-weight: 700;
  background: linear-gradient(
    45deg,
    var(--primary-blue),
    var(--soft-violet),
    var(--soft-purple)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 1rem;
  text-align: center;
  position: relative;
  padding-bottom: 1rem;
}

.section-title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: linear-gradient(45deg, var(--primary-blue), var(--soft-violet));
  border-radius: 2px;
}

.section-subtitle {
  font-size: 1.3rem;
  color: var(--text-secondary);
  margin-bottom: 3rem;
  text-align: center;
  font-weight: 400;
  line-height: 1.6;
}
/* Botones */
.btn-primary {
  background: linear-gradient(
    135deg,
    var(--primary-blue) 0%,
    var(--soft-violet) 50%,
    var(--soft-purple) 100%
  );
  border: none;
  padding: 15px 35px;
  border-radius: 30px;
  font-weight: 600;
  font-size: 1.1rem;
  color: white;
  text-decoration: none;
  display: inline-block;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 15px rgba(168, 216, 234, 0.3);
  position: relative;
  overflow: hidden;
}

.btn-primary::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  transition: left 0.5s;
}

.btn-primary:hover::before {
  left: 100%;
}

.btn-primary:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 12px 35px rgba(168, 216, 234, 0.5);
  color: white;
}

/* Responsive design */
@media (max-width: 768px) {
  .section-title {
    font-size: 2rem;
  }

  .stat-number {
    font-size: 2rem;
  }

  .search-container {
    width: 250px;
  }

  .quick-links {
    display: none;
  }

  .service-card {
    padding: 2rem;
  }

  .service-icon {
    width: 70px;
    height: 70px;
    font-size: 1.8rem;
  }
}

/* Modo oscuro para las tarjetas */
.bg-dark .service-card {
  background: linear-gradient(
    135deg,
    var(--dark-primary) 0%,
    var(--dark-secondary) 50%,
    var(--dark-primary) 100%
  );
  border-color: rgba(102, 126, 234, 0.3);
  box-shadow: 0 10px 35px rgba(0, 0, 0, 0.3);
}

.bg-dark .service-card h4 {
  color: #E2E8F0;
}

.bg-dark .service-card p {
  color: #A0AEC0;
}

.bg-dark .service-icon {
  background: linear-gradient(135deg, var(--dark-accent) 0%, var(--dark-violet) 100%);
}

/* Modo oscuro para hero */
.bg-dark .hero-section {
  background: linear-gradient(135deg, #1A202C 0%, #2D3748 50%, #1A202C 100%);
}

.bg-dark .hero-title {
  color: #E2E8F0;
}

.bg-dark .hero-description {
  color: #A0AEC0;
}

.bg-dark .stat-mini strong {
  color: var(--dark-accent);
}

.bg-dark .stat-mini span {
  color: #A0AEC0;
}

.bg-dark .floating-card {
  background: var(--dark-secondary);
  color: #E2E8F0;
  border-color: rgba(102, 126, 234, 0.3);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.4);
}

.bg-dark .floating-card i {
  color: var(--dark-accent);
}

.bg-dark .hero-illustration {
  background: linear-gradient(135deg, var(--dark-accent), var(--dark-violet));
}

/* Botones en modo oscuro */
.bg-dark .btn-primary {
  background: linear-gradient(135deg, #667eea, #9370db) !important;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4) !important;
}

.bg-dark .btn-primary:hover {
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.6) !important;
}

.bg-dark .btn-outline-primary {
  border-color: var(--dark-accent) !important;
  color: var(--dark-accent) !important;
  background: transparent !important;
}

.bg-dark .btn-outline-primary:hover {
  background: linear-gradient(135deg, var(--dark-accent), var(--dark-violet)) !important;
  border-color: var(--dark-accent) !important;
  color: white !important;
}

/* Modo oscuro para carousel */
.bg-dark .carousel-caption {
  background: linear-gradient(135deg, rgba(45, 55, 72, 0.9), rgba(74, 85, 104, 0.9));
}

.bg-dark .carousel-indicators button {
  background-color: rgba(102, 126, 234, 0.6);
  border-color: rgba(255, 255, 255, 0.6);
}

.bg-dark .carousel-indicators button.active {
  background-color: var(--dark-accent);
  border-color: #ffffff;
}

.bg-dark .carousel-control-prev-icon,
.bg-dark .carousel-control-next-icon {
  background-color: rgba(102, 126, 234, 0.8);
}

/* Modo oscuro para info cards */
.bg-dark .carousel-info-section {
  background: linear-gradient(135deg, #1A202C 0%, #2D3748 50%, #1A202C 100%);
}

.bg-dark .info-card {
  background: linear-gradient(135deg, var(--dark-primary) 0%, var(--dark-secondary) 50%, var(--dark-primary) 100%);
  border-color: rgba(102, 126, 234, 0.3);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.bg-dark .info-card:hover {
  box-shadow: 0 25px 60px rgba(102, 126, 234, 0.2);
  border-color: var(--dark-accent);
}

.bg-dark .info-card h4 {
  color: #E2E8F0;
}

.bg-dark .info-card p {
  color: #A0AEC0;
}

